	module.exports = {"data":{"site":{"siteMetadata":{"docSections":{"basics":["/basics/introduction/","/basics/quick-start-guide/","/basics/slow-start-guide/","/basics/guide-react/","/basics/guide-vue/","/basics/guide-angular/","/basics/writing-stories/","/basics/exporting-storybook/","/basics/faq/","/basics/community/","/basics/live-examples/"],"configurations":["/configurations/default-config/","/configurations/custom-webpack-config/","/configurations/custom-babel-config/","/configurations/add-custom-head-tags/","/configurations/serving-static-files/","/configurations/env-vars/","/configurations/cli-options/"],"testing":["/testing/react-ui-testing/","/testing/structural-testing/","/testing/interaction-testing/","/testing/css-style-testing/","/testing/manual-testing/"],"addons":["/addons/introduction/","/addons/using-addons/","/addons/addon-gallery/","/addons/writing-addons/","/addons/api/"]}}},"allMarkdownRemark":{"edges":[{"node":{"fields":{"slug":"/404/"},"frontmatter":{"title":"","id":null}}},{"node":{"fields":{"slug":"/addons/api/"},"frontmatter":{"title":"API","id":"api"}}},{"node":{"fields":{"slug":"/addons/addon-gallery/"},"frontmatter":{"title":"Addon Gallery","id":"addon-gallery"}}},{"node":{"fields":{"slug":"/addons/introduction/"},"frontmatter":{"title":"Intro to Addons","id":"introduction"}}},{"node":{"fields":{"slug":"/addons/using-addons/"},"frontmatter":{"title":"Using Addons","id":"using-addons"}}},{"node":{"fields":{"slug":"/basics/community/"},"frontmatter":{"title":"Community","id":"community"}}},{"node":{"fields":{"slug":"/addons/writing-addons/"},"frontmatter":{"title":"Writing Addons","id":"writing-addons"}}},{"node":{"fields":{"slug":"/basics/exporting-storybook/"},"frontmatter":{"title":"Exporting Storybook as a Static App","id":"exporting-storybook"}}},{"node":{"fields":{"slug":"/basics/faq/"},"frontmatter":{"title":"","id":null}}},{"node":{"fields":{"slug":"/basics/guide-angular/"},"frontmatter":{"title":"Storybook for Angular","id":"guide-angular"}}},{"node":{"fields":{"slug":"/basics/guide-vue/"},"frontmatter":{"title":"Storybook for Vue","id":"guide-vue"}}},{"node":{"fields":{"slug":"/basics/guide-react/"},"frontmatter":{"title":"Storybook for React","id":"guide-react"}}},{"node":{"fields":{"slug":"/basics/live-examples/"},"frontmatter":{"title":"Live Examples","id":"live-examples"}}},{"node":{"fields":{"slug":"/basics/introduction/"},"frontmatter":{"title":"Introduction","id":"introduction"}}},{"node":{"fields":{"slug":"/basics/slow-start-guide/"},"frontmatter":{"title":"Slow start guide","id":"slow-start-guide"}}},{"node":{"fields":{"slug":"/basics/quick-start-guide/"},"frontmatter":{"title":"Quick Start Guide","id":"quick-start-guide"}}},{"node":{"fields":{"slug":"/configurations/add-custom-head-tags/"},"frontmatter":{"title":"Add Custom Head Tags","id":"add-custom-head-tags"}}},{"node":{"fields":{"slug":"/basics/writing-stories/"},"frontmatter":{"title":"Writing Stories","id":"writing-stories"}}},{"node":{"fields":{"slug":"/configurations/custom-babel-config/"},"frontmatter":{"title":"Custom Babel Config","id":"custom-babel-config"}}},{"node":{"fields":{"slug":"/configurations/cli-options/"},"frontmatter":{"title":"CLI Options","id":"cli-options"}}},{"node":{"fields":{"slug":"/configurations/default-config/"},"frontmatter":{"title":"Default Config","id":"default-config"}}},{"node":{"fields":{"slug":"/configurations/custom-webpack-config/"},"frontmatter":{"title":"Custom Webpack Config","id":"custom-webpack-config"}}},{"node":{"fields":{"slug":"/configurations/serving-static-files/"},"frontmatter":{"title":"Serving Static Files","id":"serving-static-files"}}},{"node":{"fields":{"slug":"/configurations/env-vars/"},"frontmatter":{"title":"","id":null}}},{"node":{"fields":{"slug":"/testing/css-style-testing/"},"frontmatter":{"title":"CSS/Style Testing","id":"css-style-testing"}}},{"node":{"fields":{"slug":"/testing/interaction-testing/"},"frontmatter":{"title":"Interaction Testing","id":"interaction-testing"}}},{"node":{"fields":{"slug":"/testing/manual-testing/"},"frontmatter":{"title":"Manual Testing","id":"manual-testing"}}},{"node":{"fields":{"slug":"/testing/react-ui-testing/"},"frontmatter":{"title":"Introduction: React UI Testing","id":"react-ui-testing"}}},{"node":{"fields":{"slug":"/testing/structural-testing/"},"frontmatter":{"title":"Structural Testing","id":"structural-testing"}}}]},"markdownRemark":{"html":"<p>We can also use Storybook as the base for CSS/Style testing with stories as the base. For information on how to correctly apply all of your styles as well as styles from CSS frameworks like Boostrap, Semantic UI or similar check the <a href=\"https://storybook.js.org/configurations/default-config/#css-support\">configurations section</a>. Back to CSS/Style testing: let’s have a look at the following Storybook.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/storybook-screenshot-ccc1fdc32e90a2f358be7a24fc715a5d-1d34c.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 690px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 62.447960033305584%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Storybook Screenshot\"\n        title=\"\"\n        src=\"/static/storybook-screenshot-ccc1fdc32e90a2f358be7a24fc715a5d-44d6a.png\"\n        srcset=\"/static/storybook-screenshot-ccc1fdc32e90a2f358be7a24fc715a5d-f4799.png 173w,\n/static/storybook-screenshot-ccc1fdc32e90a2f358be7a24fc715a5d-b3b1d.png 345w,\n/static/storybook-screenshot-ccc1fdc32e90a2f358be7a24fc715a5d-44d6a.png 690w,\n/static/storybook-screenshot-ccc1fdc32e90a2f358be7a24fc715a5d-bc0f7.png 1035w,\n/static/storybook-screenshot-ccc1fdc32e90a2f358be7a24fc715a5d-b50e5.png 1380w,\n/static/storybook-screenshot-ccc1fdc32e90a2f358be7a24fc715a5d-0ed32.png 2070w,\n/static/storybook-screenshot-ccc1fdc32e90a2f358be7a24fc715a5d-1d34c.png 2402w\"\n        sizes=\"(max-width: 690px) 100vw, 690px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>In that, you can see the Storybook’s manager UI. It has UI elements that are not related to your app. However, there’s a way to access just a single story.</p>\n<p>For an example, let’s assume the above storybook runs on port 9009 and we can access it via <a href=\"http://localhost:9009/\">http://localhost:9009</a>.\nThen Let’s pick a single story: the “with text” story of the Button. So, in this case:</p>\n<ul>\n<li>selectedKind = Button</li>\n<li>selectedStory = with text</li>\n</ul>\n<p>Then, we can see the above story using the following URL:</p>\n<p><a href=\"http://localhost:9009/iframe.html?selectedKind=Button&#x26;selectedStory=with+text&#x26;dataId=0\">http://localhost:9009/iframe.html?selectedKind=Button&#x26;selectedStory=with+text&#x26;dataId=0</a></p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/storybook-iframe-screenshot-18d2d9ccc5a35c71f76182215d58dde7-1d34c.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 690px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 62.447960033305584%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Storybook Iframe Screenshot\"\n        title=\"\"\n        src=\"/static/storybook-iframe-screenshot-18d2d9ccc5a35c71f76182215d58dde7-44d6a.png\"\n        srcset=\"/static/storybook-iframe-screenshot-18d2d9ccc5a35c71f76182215d58dde7-f4799.png 173w,\n/static/storybook-iframe-screenshot-18d2d9ccc5a35c71f76182215d58dde7-b3b1d.png 345w,\n/static/storybook-iframe-screenshot-18d2d9ccc5a35c71f76182215d58dde7-44d6a.png 690w,\n/static/storybook-iframe-screenshot-18d2d9ccc5a35c71f76182215d58dde7-bc0f7.png 1035w,\n/static/storybook-iframe-screenshot-18d2d9ccc5a35c71f76182215d58dde7-b50e5.png 1380w,\n/static/storybook-iframe-screenshot-18d2d9ccc5a35c71f76182215d58dde7-0ed32.png 2070w,\n/static/storybook-iframe-screenshot-18d2d9ccc5a35c71f76182215d58dde7-1d34c.png 2402w\"\n        sizes=\"(max-width: 690px) 100vw, 690px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Just like that, you can access all of the stories in your Storybook.</p>\n<h2 id=\"supported-cssstyle-testing-frameworks\"><a href=\"#supported-cssstyle-testing-frameworks\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Supported CSS/Style Testing Frameworks</h2>\n<p>It will be hard to use all the frameworks we’ve <a href=\"/testing/react-ui-testing#3-css-style-testing\">mentioned</a>, but we’ll be able to use frameworks which are based on URL as the input source. (Such as <a href=\"https://github.com/garris/BackstopJS\">BackstopJS</a> and <a href=\"https://github.com/gemini-testing/gemini\">Gemini</a>)</p>\n<ul>\n<li><a href=\"https://loki.js.org/\">Loki</a>: Is a great tool for visual regression testing.</li>\n</ul>\n<blockquote>\n<p>In the future we are also planning to smooth this process with the help of <a href=\"https://github.com/storybooks/storybook/tree/master/addons/storyshots\">StoryShots</a>.</p>\n</blockquote>","fields":{"slug":"/testing/css-style-testing/"},"frontmatter":{"title":"CSS/Style Testing","id":"css-style-testing"}}},"pathContext":{"slug":"/testing/css-style-testing/"}}

